export default {
  middleware: 'auth',
  async asyncData ({ $axios, params }) {
    try {
      let getCountries = $axios.$get(`${process.env.DEV_BACKEND}/api/addresses/get/countries`)
      let getAddress = $axios.$get(`${process.env.DEV_BACKEND}/api/addresses/${params.id}`)

      let [countriesResponse, 
             addressResponse] = await Promise.all([
               getCountries,
               getAddress
             ])
        return {
          countries: countriesResponse.countries,
          address: addressResponse.address
        }
      
    } catch (error) {
      console.log(error);
    }
  },
  data () {
    return {
      country: 'Japan',
      fullName: '',
      streetAddress: '',
      city: '',
      state: '',
      zipCode: '',
      phoneNumber: '',
      deliveryInstructions: '',
      securityCode: ''
    }
  },
  
  methods: {
    async onUpdate (id) {
      try {
        let data = {
          country: this.country,
          fullName: this.fullName,
          streetAddress: this.streetAddress,
          city: this.city,
          state: this.state,
          zipCode: this.zipCode,
          phoneNumber: this.phoneNumber,
          deliveryInstructions: this.deliveryInstructions,
          securityCode: this.securityCode
        }

        let response = await this.$axios.$put(`${process.env.DEV_BACKEND}/api/addresses/${id}`, data)
        if (response.success) {
          this.$router.push('/address')
        }
      } catch (error) {
        console.log(error);
      }
    }
  }
}
